
@keyframes bounce {
    0%,  
    100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
        transform: scale(2,2);
    }
}

a{
  display: inline-block;
  color:rgb(0, 153, 255);
}

a:hover{
  animation: bounce 2.5s infinite;
}

.App-header {
  background-color: rgb(69, 83, 95);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(163, 209, 240);
}

.App-link {
  color: #61dafb;
}

body{
  font: 100% "JetBrains Mono", monospace;
}

.businessName{
  font-size: 22px;
  font-weight: bold;
}

.businessDescription{
  font-size: 12px;
  font-weight: lighter;
}

.businessImage{
  max-height: 300px;
  max-width: 300px;
  width: auto;
  height: auto;
}

button {
  background: rgb(0, 153, 255);
  width: 85px;
  height: 45px;
  font-size: 10px;
  border-radius: 5px;
  border: 0.5px solid grey;
  box-shadow: 2px 2px 2px black;
  cursor: pointer;
  margin: 12px 0px 24px 0px;
}

button:hover {
  box-shadow: 2px 2px 2px black;
  transform: scale(1.55);
  color: aqua;
  transition: 1s;
}


h1 {
display: flex;
justify-content: center;
align-items: center;
text-align: center;
margin: 24px;
}

input {
  border-radius: 4px;
}

input[type=text]:focus {
  border: 3px solid rgb(0, 153, 255);
}

li {
  margin: 14px;
  list-style: none;
  text-align: center;
}

.loginName, .loginPassword {
  width: 35%;
  border-radius: 4px;
}


.loginSubmit, .logoutButton {
  font-size: 12px;
  border-radius: 5px;
  border: 0.5px solid grey;
  box-shadow: 2px 2px 2px black;
  cursor: pointer;
  background:rgb(0, 153, 255);
}

nav {
  margin-top: 5%;
}


p {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 12px;
  background-color: rgb(69, 83, 95);
  font-weight: lighter;
  }

.registerName, .registerPassword  {
    width: 35%;
    border-radius: 4px;
  }

.registerSubmit {
  font-size: 12px;
  border-radius: 5px;
  border: 0.5px solid grey;
  box-shadow: 2px 2px 2px black;
  cursor: pointer;
  background:rgb(0, 153, 255);
}

.submit{
  background:rgb(0, 153, 255);
  width: 100px;
  height: 25px;
  font-size: 10px;
  border-radius: 5px;
  border: 0.5px solid grey;
  box-shadow: 2px 2px 2px black;
  cursor: pointer;
}

.submit:hover{
  box-shadow: 2px 2px 2px black;
  transform: scale(1.55) rotate(360deg);
  color: aqua;
  transition: .5s;
}

ul{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  
}

.url{
  font-size: 14px;
}

nav{
display:flex ;
flex-direction: row;
font-size: .5em;

}
